/* ****************************************************************************
 * ADAMA-WEB SPECIFIC
 */
[ng-message] {
  background: #fcf8e3;
  margin-bottom: 5px;
  padding: 5px 10px;
}
.famfamfam-flags {
  display: inline-block;
}
.navbar-nav > .user-menu > .dropdown-menu > li.user-header {
  height: auto;
}
.angular-table-pager {
  margin: 20px 0;
}
.angular-table-pager .pagination {
  margin: 0;
}
.angular-table-pager .pagination a {
  cursor: pointer;
  height: 2.5em;
}
.angular-table-pager > div + div {
  text-align: right;
}
.crud-search-box input + span .btn {
  -webkit-transition: border-color 0.15s ease-in-out 0s;
  transition: border-color 0.15s ease-in-out 0s;
}
.crud-search-box input:focus + span .btn {
  border-bottom: 1px solid #3c8dbc;
  border-right: 1px solid #3c8dbc;
  border-top: 1px solid #3c8dbc;
}
@media (max-width: 767px) {
  .crud-content-header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: column-reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    width: 100%;
  }
  .crud-content-header > * + * {
    margin-bottom: 10px !important;
  }
  .crud-content-header .pull-right {
    float: none !important;
  }
}
btn-create + crud-search-field form {
  margin-right: 10px;
}
